import React from 'react';

// Assets
import './style.css';

const Skills = (props) => {
  //console.log(props);
  return (
    <div className="container skills py-4 my-4"> 
        <section className="content-title py-3">
            <h3>Habilidades</h3>
        </section>
        <section className="content-skills">
            <div id="carouselExampleIndicators" className="carousel slide" data-ride="carousel">
                <ol className="carousel-indicators">
                    <li data-target="#carouselExampleIndicators" data-slide-to="0" className="active"></li>
                    <li data-target="#carouselExampleIndicators" data-slide-to="1"></li>
                </ol>
                <div className="carousel-inner">
                    <div className="carousel-item active">
                        <div className="card bg text-center card-skill p-3">
                            <blockquote className="blockquote ">
                                <h3 className="name-skill">Tecnologias</h3>
                                <ul className="list-skill">
                                    <li className="p-1">React</li>
                                    <li className="p-1">Node.js</li>
                                    <li className="p-1">Firebase</li>
                                    <li className="p-1">Bootstrap</li>
                                    <li className="p-1">Git/Github</li>                                                                    
                                </ul>
                            </blockquote>
                        </div>
                    </div>
                    <div className="carousel-item">
                        <div className="card bg text-center card-skill p-3">
                            <blockquote className="blockquote">
                                <h3 className="name-skill">Soft-skills</h3>
                                <ul className="list-skill">
                                    <li className="p-1">Empatia</li>
                                    <li className="p-1">Liderazgo</li>
                                    <li className="p-1">Proactividad</li>
                                    <li className="p-1">Metodología Ágil</li>
                                    <li className="p-1">Trabajo en equipo</li>
                                </ul>                           
                             </blockquote>
                        </div>                        
                    </div>
                </div>
                <a className="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
                    <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span className="sr-only">Previous</span>
                </a>
                <a className="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
                    <span className="carousel-control-next-icon" aria-hidden="true"></span>
                    <span className="sr-only">Next</span>
                </a>
            </div>
        </section>
        
    </div>
  );
}

export default Skills;