import React from 'react';

// Assets
import './style.css';
import mockup1 from '../Images/mockup-redsocial.png';
import mockup2 from '../Images/mockup-labcar.png';
import mockup3 from '../Images/mockup-apimovie.png';


const Work = (props) => {
  //console.log(props);
  return (
    <div className="container work py-5">
        <section className="content-title py-3">
            <h3>Proyectos</h3>          
        </section>
        <section className="content-work mt-4">
            <div className="card-group">
                <div className="card card-work mx-2">
                    <img className="card-img-top" src={mockup1} alt="mockup red social" height="200"/>
                    <div className="card-body">
                        <h5 className="card-title">Red social</h5>
                        <p className="card-text">Aplicación movil para compartir recetas de cocina</p>
                        <span><i class="fas fa-ellipsis-h"></i></span>
                        <p className="card-text">Tecnologias: JavaScript, HTML, Bootstrap, Firebase</p>
                        <a href="https://github.com/RaquelGlez/cdmx-2018-06-bc-core-am-social-network" target="_blank" className="card-link" rel="noopener noreferrer">Código</a>
                        <a href="https://raquelglez.github.io/cdmx-2018-06-bc-core-am-social-network/src" target="_blank" className="card-link" rel="noopener noreferrer">Demo</a>
                    </div>
                </div>
                <div className="card card-work mx-2">
                    <img className="card-img-top" src={mockup2} alt="mockup labcar" height="200"/>
                    <div className="card-body">
                        <h5 className="card-title">LabCar</h5>
                        <p className="card-text">Maquetación de aplicación para solicitar servicio de transporte</p>
                        <span><i class="fas fa-ellipsis-h"></i></span>
                        <p className="card-text">Tecnologias: Javascript, HTML, Bootstrap</p>
                        <a href="https://github.com/RaquelGlez/cdmx-2018-06-bc-frontend-labcar" target="_blank" className="card-link" rel="noopener noreferrer">Código</a>
                        <a href="https://raquelglez.github.io/cdmx-2018-06-bc-frontend-labcar/src" target="_blank" className="card-link" rel="noopener noreferrer">Demo</a>                    
                    </div>
                </div>
                <div className="card card-work mx-2">
                    <img className="card-img-top" src={mockup3} alt="mockup apimovie" height="200"/>
                    <div className="card-body">
                        <h5 className="card-title">Movie Blog</h5>
                        <p className="card-text">Aplicación movil para busqueda de peliculas, dado un titulo</p>
                        <span><i class="fas fa-ellipsis-h"></i></span>
                        <p className="card-text">Tecnologias: JavaScript, HTML, Bootstrap, API</p>
                        <a href="https://github.com/RaquelGlez/cdmx20181-Track-FE-themovieapi" target="_blank" className="card-link" rel="noopener noreferrer">Código</a>
                        <a href="https://raquelglez.github.io/cdmx20181-Track-FE-themovieapi/src" target="_blank" className="card-link" rel="noopener noreferrer">Demo</a>                   
                    </div>
                </div>
            </div>
          
        </section>
        
    </div>
  );
}

export default Work;